import Vue from 'vue'
import Router from 'vue-router'


Vue.use(Router)

const indexRouters = [
    {
        path: '/article',
        name: 'ArticleListContainer',
        component: resolve => require(['./components/container/ArticleListContainer.vue'], resolve)
    },
    {
        path: '/article/detail/:id',
        name: 'ReadContainer',
        component: resolve => require(['./components/container/ReadContainer.vue'], resolve)
    },
    {
        path: 'article/edit',
        name: 'WriteArticle',
        component: resolve => require(['./components/container/writeArticleContainer.vue'], resolve)
    },
    {
        path: 'login',
        name: 'Login',
        component: resolve => require(['./components/Login.vue'], resolve)
    }
];

export default new Router({
    mode: 'history',
    scrollBehavior: () => ({y: 0}),
    routes: [
        {
            path: '/',
            name: 'Index',
            component: resolve => require(['./views/Index.vue'], resolve),
            redirect: {name: 'ArticleListContainer'},
            children: indexRouters
        },
        {
            path: "*",
            redirect: {name: 'Index'}
        }
        // {
        //     path: '/about',
        //     name: 'about',
        //     // route level code-splitting
        //     // this generates a separate chunk (about.[hash].js) for this route
        //     // which is lazy-loaded when the route is visited.
        //     component: () => import(/* webpackChunkName: "about" */ './views/About.vue')
        // }
    ]
})


